<template>
    <nav class="nav" ref="navbar">
        <div class="container">
            <div class="nav__container">
                <router-link to="/"  class="logo">{{ getLogo }}</router-link>
                <ul class="list">
                    <li v-for="link in getLinks" :key="link.title">
                        <router-link :to="link.url" class="link">{{ link.title }}</router-link>
                    </li>
                </ul>
            </div>
        </div>   
    </nav>
</template>

<script>
    export default {
        computed: {
            getLinks() {
                return this.$store.state.links
            },
            getLogo() {
                return this.$store.state.logo
            }
        }
    }
</script>

